<template>
  <div >
    <div class="">
      <el-form  :model="fromData" :rules="rules" ref="fromData"  id="formBox">
        <!-- 柜号-->
        <el-form-item label='用户商家名'  prop="companyName">
          <el-input v-model="fromData.companyName"  ></el-input>
        </el-form-item>
        <!-- 提单号-->
        <el-form-item label="登陆账号" prop="account">
          <el-input v-model="fromData.account"  ></el-input>
        </el-form-item>
        <!-- 设备号-->
        <el-form-item label="登陆密码" prop="password">
          <el-input v-model="fromData.password"  ></el-input>
        </el-form-item>

      </el-form>
    </div>
    <!-- 表单-->
  </div>
</template>
<style scoped>

</style>
<script>
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  export default {
    name: 'my-divseList',
    data () {
      return {
        user: {
          imgUlr: UserImg, // 账号图片
          name: '小明管理员'
        },
        fromData: {
          companyName: null, // 商家名
          account: null, // 账号
          password: null // 密码
        },
        rules: {
          companyName: [
            { required: true, message: '请输入用户商家名', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          account: [
            { required: true, message: '请输入登陆账号', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      tableRowClassName ({row, rowIndex}) {
        var x = rowIndex % 2
        if (x === 0) {
          return ''
        } else if (x === 1) {
          return 'success-row'
        }
        return ''
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      login () {
        this.$router.push({ name: "map" })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log('提交')
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    },
    computed: {

    },
    mounted () {

    },
    components: {
    }
  }
</script>




